@font-face {
  font-family: "Open Sans";
  src: url('assets/OpenSans-Regular.ttf');
}

* {
  margin: 0;
  padding: 0;
  /* outline: 1px dashed blue; */
}
html, body {
  height: 100%;
  font-family: 'Karla', sans-serif;
}
body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 100%;
  height: 50px; /* make sure to change the wrapper padding */
  position: fixed;
  background-color: rgb(228, 98, 98);
  border-radius: 2px;
  -webkit-box-shadow: 0 4px 15px rgba(68, 68, 68, 0.6);
          box-shadow: 0 4px 15px rgba(68, 68, 68, 0.6);
  z-index: 2;
  /* margin-bottom: 500px; */
}
section.Main, footer {
  text-align: center;
}
footer {
  width: 100%;
  background-color: rgb(36, 146, 182);
  -webkit-box-shadow: 4px 0 15px rgba(68, 68, 68, 0.6);
          box-shadow: 4px 0 15px rgba(68, 68, 68, 0.6);  
}
.wrapper {
  background-color: rgb(251, 252, 243);
  /* padding-top: 50px; */
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
}
#nav_spacer {
  background-color: rgb(228, 98, 98);
  width: 100%;
  padding-bottom: 50px;
}
nav, .left_header {
  width: 380px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
nav {
  padding-right: 10px;
}
/* header > h2, nav > a { */
header > h2, nav > button {
  display: -ms-flexbox;
  display: flex;
  text-decoration: none;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 10px;
  color: white;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
header > h2 {
  word-break: keep-all;
}
nav > button {
  padding: 20px;
}
nav > button:hover {
  background-color: rgb(214, 85, 85);
}
nav > button:focus {
  outline: none !important;
  -webkit-box-shadow: none;
          box-shadow: none;
}
:focus {
  outline: none;
}
::-moz-focus-inner {
  border: 0;
}
#resume {
  background-color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 30px auto;
  width: 850px;
  height: 1100px;
  border-radius: 4px;
  color: black;
  -webkit-transition: -webkit-box-shadow .25s;
  transition: -webkit-box-shadow .25s;
  -o-transition: box-shadow .25s;
  transition: box-shadow .25s;
  transition: box-shadow .25s, -webkit-box-shadow .25s;
  -webkit-box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.hoverable:hover {
  -webkit-transition: -webkit-box-shadow .25s;
  transition: -webkit-box-shadow .25s;
  -o-transition: box-shadow .25s;
  transition: box-shadow .25s;
  transition: box-shadow .25s, -webkit-box-shadow .25s;
  -webkit-box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
#resume.hidden {
  visibility: hidden;
  width: 0;
  height: 0;
  overflow: hidden;
  margin: 0;
}

.anchor {
  display: block;
  height: 60px; /*same height as header*/
  margin-top: -60px; /*same height as header*/
  visibility: hidden;
}
#welcome {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgb(79, 124, 176);
  color: rgb(243, 204, 75);
  font-size: 20px;
  padding-bottom: 30px;
  -webkit-box-shadow: 0 4px 5px rgba(68, 68, 68, 0.2);
          box-shadow: 0 4px 5px rgba(68, 68, 68, 0.2);
}
#welcome > .portraits {
  width: 100%;
  height: 435px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-image: url('assets/world2.jpg');
  /* background-position: 64% 68%; */
  background-size: 100%;
  -webkit-box-shadow: 0 2px 5px rgba(68, 68, 68, 0.1);
          box-shadow: 0 2px 5px rgba(68, 68, 68, 0.1);
}
#welcome > h3 > span {
  color: white;
  line-height: 28px;  
}
#welcome > h3 > span#phrase {
  color: rgb(243, 204, 75);
}
#portrait {
  border-radius: 400px;
  border: 3px rgb(224, 224, 201) solid;
  /* border: 8px rgb(118, 135, 167) solid; */
  width: 250px;
  height: 250px;
  -o-object-fit: cover;
     object-fit: cover;
  margin: 20px auto;
  -webkit-box-shadow: 0 4px 5px rgba(68, 68, 68, 0.4);
          box-shadow: 0 4px 5px rgba(68, 68, 68, 0.4);
}
.social_label > img, .portrait_label > img {
  width: 30px;
  margin-bottom: 10px;
  margin-right: 15px;
  display: inline-block;
  vertical-align: middle;  
  -webkit-transform: rotate(200deg);  
      -ms-transform: rotate(200deg);  
          transform: rotate(200deg)
}
.social_label > img {
  -webkit-transform: rotate(-10deg);
      -ms-transform: rotate(-10deg);
          transform: rotate(-10deg)
}
.portrait_label > h1, .social_label > h3 {
  display: inline-block;
  margin: 15px 0;
}
.portrait_label {
  font-size: 27px;
}
#about_me, #resume_section {
  font-size: 20px;
  height: 100%;
}
#projects {
  margin-bottom: 30px;  
}
.social_links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 20px auto;
  padding-top: 15px;
}
.spacer {
  height: 10px;
  width: 100px;
}
.flash {
  visibility: hidden;
}
#here {
  border-radius: 13px;
  width: 60px;
  height: 40px;
  -o-object-fit: cover;
     object-fit: cover;
  margin: 0 10px;
  overflow: hidden;
}
span.spacer {
  display: inline-block;
  width: 12px;
  height: 25px;
  background-color: rgb(201, 154, 93);
  vertical-align: middle;
  margin-top: -5px;
}

.underline {
  width: 90%;
  height: 2px;
  border-bottom: 0.5px solid silver;
  opacity: 0.6;
  margin: 15px auto;
  -webkit-box-shadow:0.5px 0.5px 0.5px silver;
          box-shadow:0.5px 0.5px 0.5px silver;
}
.card, .gato {
  margin: 15px auto;  
  width: 90%;
  padding: 40px 20px;
  padding-top: 10px;
  -webkit-box-shadow: 0 4px 5px rgba(68, 68, 68, 0.2);
          box-shadow: 0 4px 5px rgba(68, 68, 68, 0.2);
  background-color: #f2ece8;
  border-radius: 8px;
}
.project, #btn_remove_gato {
  cursor: pointer;
  /* padding-bottom: 100px; */
}

.card > h1 {
  font-size: 40px;
  color: rgb(66, 113, 214);
  margin: 15px auto;
}
#projects_container {
  margin-top: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
#projects_container > .card {
  width: 35%;
  /* height: 200px; */
  background-color: white;
}

/* .portraits:hover + .portrait_label {
  z-index: 4;
}
#portrait:hover {
  z-index: 3;
  width: 650px;
  height: 650px;
  border: none;
} */

.content {
  padding: 20px;
  -webkit-box-shadow: 0 0 0 0;
          box-shadow: 0 0 0 0;
  opacity: 0.77;
  width: 70%;
  margin: 0 auto;
  margin-bottom: -20px;
  word-wrap: break-word;
}
#about_me p, #about_me p {
  font-size: 0.9em;
}
#resume_pdf {
  width: 100%;
}
div.btn {
  display: -ms-flexbox;
  display: flex;
  margin: auto;
  border-radius: 5px;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  /* width: 110px; */
  height: 35px;
  background-color: #f2ece8;
}
div.btn > a {
  display: block;
  width: 100%;
  padding: 5px 20px;
}
#resume_section a {
  text-decoration: none;
}
div.btn:hover {
  background-color: #e6dad2;
}
#download {
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  width: 450px;
  -ms-flex-align: center;
      align-items: center;
}
.Footer {
  margin-bottom: -20px;
  padding-bottom: 12px;
}
@media screen and (min-width: 1400px) {
  .portraits:hover + .portrait_label {
    z-index: 4;
  }
  #portrait:hover {
    z-index: 3;
    width: 650px;
    height: 650px;
    border: none;
  }
  #projects_container > .card {
    width: 60%;
  }
  .gato {
    width: 40%;
  }
}

#portrait_label.hover_label {
  z-index: 4;
}
#portrait.hover_portrait {
  z-index: 3;
  width: 650px;
  height: 650px;
  border: none;
}

@media screen and (max-width: 1400px) {
  .left_header {
    width: 0;
    height: 0;
    visibility: hidden;
    margin: 0;
  }
  header {
    height: 110px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }
  header > h2 {
    width: 350px;
  }
  #resume {
    visibility: hidden !important;
    width: 0 !important;
    height: 0 !important;
    overflow: hidden;
    margin: 0;
  }
  #projects_container > .card {
    width: 60%;
  }
  .content {
    width: 85%;
  }
  #header_nav.shrink {
    /* display: none; */
    height: 0;
    /* visibility: hidden; */
    opacity: 0;
    -webkit-transition: height 0.2s, visibility 0.25s, opacity 0.2s linear;
    -o-transition: height 0.2s, visibility 0.25s, opacity 0.2s linear;
    transition: height 0.2s, visibility 0.25s, opacity 0.2s linear;
  }
  .gato {
    width: 60%;
  }
}

@media screen and (max-width: 760px) {
  header > h2 {
    width: 0;
    visibility: hidden;
  }
  nav {
    width: 100%;
  }
  nav > button {
    margin: 0 auto;
    padding: 0 30px;
  }
  #welcome > .portraits {
    height: 400px;
  }
  #welcome > h3 {
    height: 95px;
    width: 285px;
    overflow-wrap: normal;
  }
  #portrait.hover_portrait {
    border: none;
    margin: 0;
    margin-left: -2px;
    border-radius: 0;
    width: 100%;
    height: 700px;
  }
  #projects_container > .card {
    width: 80%;
  }
  .card {
    padding-right: 0;
    padding-left: 0;
  }
  .content {
    width: 98%;
  }
  #download {
    -ms-flex-direction: column;
        flex-direction: column;
    width: 98%;
  }
  .gato {
    width: 80%;
  }
}

@media screen and (max-width: 420px) {
  nav {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  nav > button {
    margin: 0 auto;
    padding: 5px 110px;
  }
  nav > button:last-of-type {
    padding-bottom: 5px;
  }
  .card {
    width: 88%;
    padding: 20px 15px;
    padding-top: 10px;
  }
  #projects_container > .card {
    width: 120%;
  }
  .gato {
    width: 90%;
  }
}
#removal_plea {
  padding-top: 15px;
}
#btn_remove_gato {
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  color: whitesmoke;
  background-color: rgba(24, 121, 153, 0.808);
}
#btn_remove_gato:hover {
  background-color: rgba(17, 95, 121, 0.808);
  color: whitesmoke;
}


